.container {
  display: flex;
  flex-direction: column;
  padding: 5px;
  margin: 0 auto;
  margin-top: 30px;
  max-width: 300px;
  background-color: rgb(34, 134, 134);
  box-shadow: 0 8px 11px -6px #222;
  border-radius: 5px;
}

body {
  background-color: rgb(82, 67, 44);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
